<template>
  <div>
      
      <h3 class="subtitle">Login Page</h3>
        <div>
            <input class="input is-size-4" v-model="username" placeholder="Username.."/>
            <br>
            <input class="input is-size-4" v-model="password" type="password" placeholder="Password..."/>
        </div>
      <button class="button is-primary is-medium" @click="login()">Login</button>
      <br>
      <br>
      <p>New here?</p>
      <button class="button is-link" @click="getUsers()"> signup</button>
  </div>
</template>

<script>
import axios from "axios";

export default {
    name:"Login",

    data() {
        return  {
            username: "",
            password: ""
        }
    },
    created(){
        const token = localStorage.getItem('token')
        if (token){
            axios.post("/users/checktoken")
            .then((res) => {
                if (res.data.message == "valid token"){
                    this.$router.replace("/")
                }
            })
        }
    },

    methods: {
        login(){
            axios.post("/users/maketoken", {name: this.username, password: this.password})
            .then((res) => {
                if(res.data.token){
                    localStorage.setItem('token', res.data.token)
                    localStorage.setItem('name', res.data.name)
                } else { alert("invalid name or password!") 
                this.username = ""
                this.password = ""}
                const token = localStorage.getItem('token')
                if (token) {
                axios.defaults.headers.common['Authorization'] = "Bearer " + token
            
                this.$router.replace('/')
               
                }
                
            })
            .catch((err) => {
                console.log(err)
            })
        },
        getUsers(){
            
            this.$router.replace('/signup')
  
        }
    }
}
</script>

<style lang="scss" scoped>
.input {
    align-self:center;
   
    font-size: 35px;
    
}
</style>